import React,{Component} from 'react';
import './Jumbotron.css';

import PhotoshootCover from '../images/PhotoshootCover.png';



class Jumbotron extends Component{
    render(){
        var styles ={
            backgroundImage : PhotoshootCover,
            
            
        };
        return(
            <div>
               
                <div class="Jumbotron" style={styles}  >
               
              
        <div className="container">
          <h1 className="display-3"></h1>
             <p className="body" style={{color:'rgb(223,0,0)',fontSize:'large'}}>You can play my games over at Itch.io </p>
          <p><a className="btn btn-red btn-lg" href="https://paranormal09.itch.io/" role="button"  >Itch.io</a></p>
        </div>
      </div>
            </div>

            
        );
       
    }
}

export default Jumbotron