import React, {Component} from 'react';
import './Home.css';
import * as emailjs from "emailjs-com";
import Navbar from '../components/Navbar.jsx';
import {Button, Form, FormGroup,Label,Input,FormText} from 'reactstrap';
//import Footer from '../components/Footer.jsx'; 


class Contact extends Component{
    
    render(){
        function sendemail(e){
          e.preventDefault();

      emailjs.sendForm('gmail', 'mywebpage', e.target, 'user_TlRApCZcjLE3c1Ov0jug2')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
    }
        return(
            <div>
                  <Navbar/>
            <div className='container'>  
        
            <body style={{paddingBottom:'900px'}}>
                <div><b style={{color:"white",fontSize:'xxx-large'}}>To get in contact with me fill out the form below and Ill message you back as soon as possible </b> 
               <form className="formgroup" onSubmit={sendemail} >
               
               <Label style={{color:'white',fontSize:"Large"}}>email:</Label>
                <Input type="text" id="formgroup" name="email" placeholder="Enter Email" ></Input>
                <Label style={{color:'white',fontSize:"Large"}}>message:</Label>
                <Input type="textarea" id="formgroup" name="message" placeholder="type your message here "></Input>
                <Input type="submit" style={{marginTop:'20px'}}/>
               </form>
                </div>
                </body>
            
            </div>
            </div>
    
        );
    }
}

export default Contact